<script setup lang="ts">
import { PropType, computed, ref, watch } from 'vue'
import { useForm } from 'vuestic-ui'
import { Therapist, TherapistRole } from '../types'
import TherapistAvatar from './TherapistAvatar.vue'
import { useProjects } from '../../projects/composables/useProjects'
import { validators } from '../../../services/utils'

const props = defineProps({
    therapist: {
        type: Object as PropType<Therapist | null>,
        default: null,
    },
    saveButtonLabel: {
        type: String,
        default: 'Save',
    },
})

const defaultNewTherapist: Therapist = {
    id: -1,
    avatar: '',
    fullname: '',
    role: 'therapist',
    username: '',
    notes: '',
    email: '',
    active: true,
    projects: [],
}

const newTherapist = ref<Therapist>({ ...defaultNewTherapist })

const isFormHasUnsavedChanges = computed(() => {
    return Object.keys(newTherapist.value).some((key) => {
        if (key === 'avatar' || key === 'projects') {
            return false
        }

        return (
            newTherapist.value[key as keyof Therapist] !==
            (props.therapist ?? defaultNewTherapist)?.[key as keyof Therapist]
        )
    })
})

defineExpose({
    isFormHasUnsavedChanges,
})

watch(
    () => props.therapist,
    () => {
        if (!props.therapist) {
            return
        }

        newTherapist.value = {
            ...props.therapist,
            avatar: props.therapist.avatar || '',
        }
    },
    { immediate: true },
)

const avatar = ref<File>()

const makeAvatarBlobUrl = (avatar: File) => {
    return URL.createObjectURL(avatar)
}

watch(avatar, (newAvatar) => {
    newTherapist.value.avatar = newAvatar ? makeAvatarBlobUrl(newAvatar) : ''
})

const form = useForm('add-therapist-form')

const emit = defineEmits(['close', 'save'])

const onSave = () => {
    if (form.validate()) {
        emit('save', newTherapist.value)
    }
}

const roleSelectOptions: { text: Capitalize<TherapistRole>; value: TherapistRole }[] = [
    { text: 'Admin', value: 'admin' },
    { text: 'Therapist', value: 'therapist' },
    { text: 'Owner', value: 'owner' },
]

const { projects } = useProjects({ pagination: ref({ page: 1, perPage: 9999, total: 10 }) })
</script>

<template>
    <VaForm
        v-slot="{ isValid }"
        ref="add-therapist-form"
        class="flex-col justify-start items-start gap-4 inline-flex w-full"
    >
        <VaFileUpload
            v-model="avatar"
            type="single"
            hide-file-list
            class="self-stretch justify-start items-center gap-4 inline-flex"
        >
            <TherapistAvatar :therapist="newTherapist" size="large" />
            <VaButton preset="primary" size="small">Add image</VaButton>
            <VaButton
                v-if="avatar"
                preset="primary"
                color="danger"
                size="small"
                icon="delete"
                class="z-10"
                @click.stop="avatar = undefined"
            />
        </VaFileUpload>
        <div class="self-stretch flex-col justify-start items-start gap-4 flex">
            <div class="flex gap-4 flex-col sm:flex-row w-full">
                <VaInput
                    v-model="newTherapist.fullname"
                    label="Full name"
                    class="w-full sm:w-1/2"
                    :rules="[validators.required]"
                    name="fullname"
                />
                <VaInput
                    v-model="newTherapist.username"
                    label="Username"
                    class="w-full sm:w-1/2"
                    :rules="[validators.required]"
                    name="username"
                />
            </div>
            <div class="flex gap-4 flex-col sm:flex-row w-full">
                <VaInput
                    v-model="newTherapist.email"
                    label="Email"
                    class="w-full sm:w-1/2"
                    :rules="[validators.required, validators.email]"
                    name="email"
                />
                <VaSelect
                    v-model="newTherapist.projects"
                    label="Projects"
                    class="w-full sm:w-1/2"
                    :options="projects"
                    :rules="[validators.required]"
                    name="projects"
                    text-by="project_name"
                    track-by="id"
                    multiple
                    :max-visible-options="2"
                />
            </div>

            <div class="flex gap-4 w-full">
                <div class="w-1/2">
                    <VaSelect
                        v-model="newTherapist.role"
                        label="Role"
                        class="w-full"
                        :options="roleSelectOptions"
                        :rules="[validators.required]"
                        name="role"
                        value-by="value"
                    />
                </div>

                <div class="flex items-center w-1/2 mt-4">
                    <VaCheckbox v-model="newTherapist.active" label="Active" class="w-full" name="active" />
                </div>
            </div>

            <VaTextarea v-model="newTherapist.notes" label="Notes" class="w-full" name="notes" />
            <div class="flex gap-2 flex-col-reverse items-stretch justify-end w-full sm:flex-row sm:items-center">
                <VaButton preset="secondary" color="secondary" @click="$emit('close')">Cancel</VaButton>
                <VaButton :disabled="!isValid" @click="onSave">{{ saveButtonLabel }}</VaButton>
            </div>
        </div>
    </VaForm>
</template>
