import { Ref, ref, unref, watch } from 'vue'
import {
    getTherapists,
    updateTherapist,
    addTherapist,
    removeTherapist,
    type Filters,
    Pagination,
    Sorting,
} from '../../../data/pages/therapists'
import { Therapist } from '../types'
import { watchIgnorable } from '@vueuse/core'

const makePaginationRef = () => ref<Pagination>({ page: 1, perPage: 10, total: 0 })
const makeSortingRef = () => ref<Sorting>({ sortBy: 'fullname', sortingOrder: null })
const makeFiltersRef = () => ref<Partial<Filters>>({ isActive: true, search: '' })

export const useTherapists = (options?: {
    pagination?: Ref<Pagination>
    sorting?: Ref<Sorting>
    filters?: Ref<Partial<Filters>>
}) => {
    const isLoading = ref(false)
    const therapists = ref<Therapist[]>([])

    const { filters = makeFiltersRef(), sorting = makeSortingRef(), pagination = makePaginationRef() } = options || {}

    const fetch = async () => {
        isLoading.value = true
        const { data, pagination: newPagination } = await getTherapists({
            ...unref(filters),
            ...unref(sorting),
            ...unref(pagination),
        })
        therapists.value = data

        ignoreUpdates(() => {
            pagination.value = newPagination
        })

        isLoading.value = false
    }

    const { ignoreUpdates } = watchIgnorable([pagination, sorting], fetch, { deep: true })

    watch(
        filters,
        () => {
            // Reset pagination to first page when filters changed
            pagination.value.page = 1
            fetch()
        },
        { deep: true },
    )

    fetch()

    return {
        isLoading,

        filters,
        sorting,
        pagination,

        therapists,

        fetch,

        async add(therapist: Therapist) {
            isLoading.value = true
            await addTherapist(therapist)
            await fetch()
            isLoading.value = false
        },

        async update(therapist: Therapist) {
            isLoading.value = true
            await updateTherapist(therapist)
            await fetch()
            isLoading.value = false
        },

        async remove(therapist: Therapist) {
            isLoading.value = true
            await removeTherapist(therapist)
            await fetch()
            isLoading.value = false
        },
    }
}
