import { sleep } from '../../services/utils'
import { Therapist } from './../../pages/therapists/types'
import therapistsDb from './therapists-db.json'

export const therapists = therapistsDb as Therapist[]

// Simulate API calls

export type Pagination = {
    page: number
    perPage: number
    total: number
}

export type Sorting = {
    sortBy: keyof Therapist | undefined
    sortingOrder: 'asc' | 'desc' | null
}

export type Filters = {
    isActive: boolean
    search: string
}

const getSortItem = (obj: any, sortBy: string) => {
    if (sortBy === 'projects') {
        return obj.projects.map((project: any) => project.project_name).join(', ')
    }

    return obj[sortBy]
}

export const getTherapists = async (filters: Partial<Filters & Pagination & Sorting>) => {
    await sleep(1000)
    const { isActive, search, sortBy, sortingOrder } = filters
    let filteredTherapists = [...therapists]

    filteredTherapists = filteredTherapists.filter((therapist) => therapist.active === isActive)

    if (search) {
        filteredTherapists = filteredTherapists.filter((therapist) =>
            therapist.fullname.toLowerCase().includes(search.toLowerCase()),
        )
    }

    // Add empty projects array for now
    filteredTherapists = filteredTherapists.map((therapist) => ({
        ...therapist,
        projects: [],
    }))

    if (sortBy && sortingOrder) {
        filteredTherapists = filteredTherapists.sort((a, b) => {
            const first = getSortItem(a, sortBy)
            const second = getSortItem(b, sortBy)
            if (first > second) {
                return sortingOrder === 'asc' ? 1 : -1
            }
            if (first < second) {
                return sortingOrder === 'asc' ? -1 : 1
            }
            return 0
        })
    }

    const { page = 1, perPage = 10 } = filters || {}
    return {
        data: filteredTherapists.slice((page - 1) * perPage, page * perPage),
        pagination: {
            page,
            perPage,
            total: filteredTherapists.length,
        },
    }
}

export const addTherapist = async (therapist: Therapist) => {
    await sleep(1000)
    therapists.unshift(therapist)
}

export const updateTherapist = async (therapist: Therapist) => {
    await sleep(1000)
    const index = therapists.findIndex((t) => t.id === therapist.id)
    therapists[index] = therapist
}

export const removeTherapist = async (therapist: Therapist) => {
    await sleep(1000)
    therapists.splice(
        therapists.findIndex((t) => t.id === therapist.id),
        1,
    )
}
